<template>
  <div id="faq" class="w3-container w3-card-4 w3-light-grey w3-margin">
    <h1>
      <b>F A Q</b>
    </h1>
    <br>
    <VueFaqAccordion :items="myItems" style="margin-left:15%; padding-right:10% white-space:pre;" />
  </div>
</template>
 
<script>
import VueFaqAccordion from "vue-faq-accordion";

export default {
  components: {
    VueFaqAccordion
  },
  data() {
    return {
      myItems: [
        {
          title: "   ?",
          value:
            "              ."+
            "              ",
          category: ""
        },
        {
          title: "   ?",
          value:
            "      1:1     ."+
            "       1:1  .",
          category: ""
        },
        {
          title: "  ?",
          value:
            "             .\n"+
            "             .\n"+
            ",   , , , ,  / ,  (    ),   ,     .  ",
          category: ""
        },
        {
          title: "  ?",
          value: "         .\n"+
          "     ,          .\n"+
          "    ,      .",
          category: ""
        },
      ]
    };
  },
  props: {
    items: {
      type: Array,
      required: true
    },

    questionProperty: {
      type: String,
      default: "title"
    },
    answerProperty: {
      type: String,
      default: "value"
    },
    tabName: {
      type: String,
      default: "category"
    },
    activeColor: {
      type: String,
      default: "#D50000"
    },
    borderColor: {
      type: String,
      default: "#9E9E9E"
    },

    fontColor: {
      type: String,
      default: "#FFFFFF"
    }
  }
};
</script>

